.dara-hero {
	position: relative;
	padding: 100px 0 15px;
	color: #000;
	overflow: hidden;
	@include rmin(768) {
		padding: 175px 0 15px; }
	&.dara-hero--sm-padding {
		padding: 50px 0 70px;
		@include rmin(1200) {
			padding: 75px 0 80px; } }
	&__wrap {
		max-width: 1437px;
		margin: 0 auto; }
	&__bl {
		position: relative;
		max-width: 1020px;
		margin: 0 auto 60px; }
	&__head {
		margin-bottom: 30px;
		@include gilroy-medium;
		font-size: 16px;
		line-height: 1.08;
		@include rmin(576) {
			margin-bottom: 44px;
			font-size: 24px; }
		@include rmin(1200) {
			font-size: 26px; } }
	&__title {
		margin-bottom: 15px;
		@include caslon-semibold;
		font-size: 38px;
		letter-spacing: -3px;
		line-height: 1.1;
		@include rmin(576) {
			margin-bottom: 7px;
			font-size: 45px;
			line-height: 1.16; }
		@include rmin(1200) {
			font-size: 70px; }
		span {
			color: #167A65; } }
	&__subtitle {
		max-width: 730px;
		margin: 0 auto; }
	&__desc {
		margin-bottom: 25px;
		text-transform: uppercase;
		@include gilroy-bold;
		font-size: 16px;
		color: #167A65;
		line-height: 1.375;
		letter-spacing: 1.71px;
		@include rmin(1200) {
			margin-bottom: 55px; } }
	&__img {
		position: relative;
		display: block;
		left: 50%;
		width: 500px;
		margin: 0 auto;
		transform: translateX(-50%);
		@include rmin(576) {
			position: static;
			width: auto;
			max-width: 100%;
			transform: translateX(0); } } }


.dara-subscription {
	margin-bottom: 40px;
	padding: 0 10px;
	@include rmin(1200) {
		padding: 0 40px; }
	&__in {
		max-width: 1360px;
		margin: 0 auto;
		padding: 40px 0;
		background: #FFF;
		@include rmin(768) {
			padding: 50px 15px; }
		@include rmin(1200) {
			padding: 64px 15px 97px; } }
	&__title {
		margin-bottom: 0;
		@include caslon-semibold;
		font-size: 31px;
		letter-spacing: -2.14px;
		line-height: 1.08;
		@include rmin(768) {
			font-size: 40px; }
		@include rmin(1200) {
			font-size: 50px; } }
	&__head {
		max-width: 760px;
		margin: 0 auto 35px;
		text-align: center;
		@include rmin(768) {
			margin-bottom: 69px; } }
	&__welcome {
		margin-bottom: 30px;
		@include gilroy-bold;
		text-transform: uppercase;
		font-size: 16px;
		color: #167A65;
		letter-spacing: 1.71px;
		@include rmin(768) {
			margin-bottom: 38px; } } }


.dara-step {
	max-width: 300px;
	margin: 0 auto 30px;
	text-align: center;
	@include gilroy-medium;
	font-size: 15px;
	@include rmin(1200) {
		font-size: 16px; }
	&__img {
		width: 100%;
		max-width: 100px;
		height: 100px;
		margin: 0 auto 31px;
		object-fit: contain;
		font-size: 'object-fit: contain';
		@include rmin(768) {
			margin-bottom: 36px; } }
	&__pic {
		display: flex;
		min-height: 100px;
		justify-content: center;
		align-items: center;
		@include rmin(768) {
			min-height: 160px; } }
	&__icon {
		display: inline-block;
		background-repeat: no-repeat;
		background-size: contain;
		&--money {
			width: 47px;
			height: 54px;
			background-image: url(../img/money.png); }
		&--truck {
			width: 61px;
			height: 27px;
			background-image: url(../img/truck.png); }
		&--handshake {
			width: 60px;
			height: 51px;
			background-image: url(../img/handshake.png); } }
	&__title {
		margin-bottom: 6px;
		text-transform: uppercase;
		@include gilroy-bold;
		color: #167A65;
		font-size: 14px;
		line-height: 1.375;
		letter-spacing: 1.71px;
		@include rmin(768) {
			font-size: 16px; } }
	&__info {
		max-width: 275px;
		margin: 0 auto;
		line-height: 1.625; } }


.dara-about {
	max-width: 1440px;
	margin: 0 auto 40px;
	background: #fff;
	color: #181818;
	overflow: hidden;
	&__section {
		margin: 0 -15px; }
	&__bl {
		@include rmin(768) {
			height: 100%;
			min-height: 400px; }
		@include rmin(1200) {
			display: flex;
			min-height: 600px; }
		&--text {
			display: flex;
			align-items: center;
			height: 100%;
			padding: 40px 15px;
			@include rmin(768) {
				padding: 40px 20px; } }
		&--img {
			position: relative;
			margin: 0 -15px;
			@include rmin(1200) {
				align-items: stretch; } } }
	&__in {
		max-width: 430px;
		margin: 0 auto;
		text-align: center;
		@include rmin(768) {
			text-align: left; }
		@include rmin(1200) {
			max-width: 470px; } }
	&__img {
		display: block;
		width: 100%;
		object-fit: cover;
		font-family: 'object-fit: cover;';
		@include rmin(768) {
			position: absolute;
			top: 0;
			left: 0;
			height: 100%; }
		@include rmin(1200) {
			position: static; } }
	&__desc {
		margin-bottom: 30px;
		text-transform: uppercase;
		@include gilroy-bold;
		font-size: 16px;
		color: #167A65;
		line-height: 1.375;
		letter-spacing: 1.71px;
		@include rmin(1200) {
			margin-bottom: 44px; } }
	&__title {
		margin-bottom: 14px;
		@include caslon-semibold;
		font-size: 30px;
		line-height: 1.08;
		@include rmin(768) {
			font-size: 40px; }
		@include rmin(1200) {
			font-size: 50px; } }
	&__text {
		max-width: 509px;
		@include gilroy-medium;
		font-size: 15px;
		@include rmin(768) {
			font-size: 16px; }
		@include rmin(1200) {
			font-size: 18px; }
		&:not(:last-child) {
			margin-bottom: 30px;
			@include rmin(1200) {
				margin-bottom: 57px; } }
		p {
			margin-bottom: 22px;
			&:last-child {
				margin-bottom: 0; } } } }


.dara-feature {
	padding: 0 10px;
	color: #181818;
	@include rmin(1200) {
		padding: 0 40px; }
	&__in {
		max-width: 1360px;
		margin: 0 auto;
		padding: 45px 15px 30px;
		background: #FFF;
		@include rmin(768) {
			padding: 45px 15px 55px; } }
	&__head {
		margin-bottom: 30px;
		text-align: center;
		@include rmin(1200) {
			margin-bottom: 51px; } }
	&__desc {
		text-transform: uppercase;
		margin-bottom: 20px;
		@include gilroy-bold;
		font-size: 16px;
		color: #167A65;
		line-height: 1.375;
		letter-spacing: 1.71px;
		@include rmin(1200) {
			margin-bottom: 43px; } }
	&__title {
		margin-bottom: 20px;
		@include caslon-semibold;
		font-size: 37px;
		line-height: 1.08;
		letter-spacing: -2.04px;
		@include rmin(768) {
			font-size: 45px; }
		@include rmin(1200) {
			margin-bottom: 28px;
			font-size: 50px; } }
	&__subtitle {
		@include gilroy-medium;
		font-size: 15px;
		@include rmin(768) {
			font-size: 18px; } }
	&__list {
		max-width: 1000px;
		margin: 0 auto 40px;
		@include rmin(1200) {
			margin-bottom: 60px; } }
	&__img {
		margin-bottom: 40px;
		@include rmin(768) {
			margin-bottom: 87px; } } }



.dara-checklist {
	max-width: 235px;
	margin: 0 auto;
	text-align: left;
	@include gilroy-bold;
	font-size: 18px;
	color: #167A65;
	line-height: 1.2;
	@include rmin(768) {
		max-width: 100%;
		margin: auto;
		font-size: 20px; }
	@include rmin(1200) {
		font-size: 25px; }
	&--horizontal {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-evenly; }
	&__option {
		position: relative;
		margin-bottom: 10px;
		padding-left: 35px;
		@include rmin(768) {
			margin-bottom: 18px;
			padding-left: 55px; }
		&:before {
			content: "";
			position: absolute;
			top: 4px;
			left: 0;
			width: 20px;
			height: 14px;
			background: url(../img/check-green.svg) 0 0 no-repeat;
			background-size: contain;
			@include rmin(768) {
				width: 22px;
				height: 16px; }
			@include rmin(1200) {
				top: 8px; } }
		&:last-child {
			margin-bottom: 0; } } }






