.dara-header {
	position: fixed;
	z-index: 10;
	top: 0;
	left: 0;
	right: 0;
	background: #F2F2F2;
	transition: background 0.2s ease;
	&.is-active {
		background: #FFF;
		.dara-header__logo svg path {
			fill: #000; }
		.dara-header__menu-ico {
			color: #000;
			&:hover,
			&:visited {
				color: #000; } } }
	&.is-start {
		.dara-button--header-start {
			opacity: 1;
			visibility: visible; } }
	&--white-bg {
		background: #fff; }
	&--no-bg {
		background: #FFF;
		@include rmin(768) {
			background: none; } }
	&--mob-sm-height {
		.dara-header__in {
			min-height: 40px;
			padding: 2px 5px;
			border-bottom: 1px solid #F2F2F2;
			@include rmin(1025) {
				min-height: 57px;
				border-bottom-color: transparent;
				padding: 11px 5px 5px; } }
		.dara-header__logo {
			width: 52px;
			margin-top: 8px;
			@include rmin(1025) {
				width: 76px;
				margin-top: 3px; } } }
	&__in {
		min-height: 57px;
		padding: 11px 5px 5px;
		@include rmin(1100) {
			padding: 11px 25px 5px; } }
	&__checkout {
		display: none;
		margin-right: 10px;
		@include rmin(1025) {
			display: flex;
			align-items: center; }
		@include rmin(1100) {
			margin-right: 20px; } }
	&__info {
		display: none;
		min-width: 200px;
		margin-right: 10px;
		line-height: 0.8;
		@include br-medium;
		@include rmin(1025) {
			display: block; }
		@include rmin(1100) {
			margin-right: 20px; } }
	&__price {
		font-size: 22px;
		color: #000;
		letter-spacing: -0.31px;
		line-height: 1; }
	&__note {
		font-size: 13px;
		color: #1078EE;
		letter-spacing: -0.18px; }
	&__menu-ico {
		display: inline-block;
		margin-top: 4px;
		color: #000;
		&:hover,
		&:visited {
			color: #000; }
		&--absolute {
			position: absolute;
			top: 50%;
			left: 15px;
			margin-top: -10px;
			line-height: 1; }
		svg {
			vertical-align: middle;
			width: 16px;
			height: 17px; } }
	&__help-ico {
		display: inline-block;
		margin-top: 7px;
		font-size: 20px;
		color: #4A4A4A;
		line-height: 0.7;
		@include tr(color 0.2s ease);
		&:hover {
			color: lighten(#4A4A4A, 20%); } }
	&__back-ico {
		display: inline-block;
		margin-top: 11px;
		font-size: 13px;
		stroke: #FE0066;
		line-height: 0.7; }
	&__logo {
		display: inline-block;
		width: 76px;
		margin-top: 3px;
		transition: all 0.2s ease;
		&--move {
			@include rmin(768) {
				width: 125px;
				transform: translate3d(0, 82px, 0); } }
		&--text {
			width: auto;
			margin-top: 5px;
			@include caslon;
			font-size: 33px;
			color: #167A65;
			letter-spacing: -2.51px;
			line-height: 0.85;
			@include rmin(576) {
				margin-top: 0;
				font-size: 44px; }
			&:hover,
			&:focus {
				text-decoration: none;
				color: #167A65; } }
		img,
		svg {
			display: block;
			width: 100%; } }
	&__bl {
		display: flex;
		justify-content: flex-end;
		align-items: center;
		text-align: right; } }



.dara-header-list {
	margin-bottom: 0;
	padding-top: 5px;
	&--with-button {
		display: flex;
		justify-content: flex-end;
		min-height: 30px; }
	&__option {
		position: relative;
		margin-right: 20px;
		&:last-child {
			margin-right: 0; } }
	&__item {
		display: inline-block;
		@include gilroy-bold;
		font-size: 16px;
		color: #181818;
		transition: color 0.2s ease;
		&:hover,
		&:visited,
		&:focus {
			text-decoration: none;
			color: #167A65; }
		&--active {
			color: #167A65; }
		&--sign {
			color: #FE0066;
			&:hover,
			&:visited {
				color: #FE0066; } } } }


.dara-menu {
	position: fixed;
	z-index: 12;
	top: 0;
	bottom: 0;
	width: 100%;
	background: #167A65;
	overflow: auto;
	transform: translate3d(-100%, 0, 0);
	transition: transform 0.3s ease-in;
	@include rmin(500) {
		width: 300px; }
	&.is-active {
		transform: translate3d(0,0,0); }
	&__in {
		padding: 20px; }
	&__top {
		position: relative;
		margin-bottom: 40px;
		padding: 0 20px;
		text-align: center; }
	&__close {
		position: absolute;
		top: 50%;
		right: 0;
		font-size: 16px;
		stroke: #fff;
		line-height: 0.7;
		transform: translateY(-50%); }
	&__option {
		margin-bottom: 20px;
		@include gilroy-bold; }
	&__link {
		color: #FFF;
		font-size: 17px;
		&:hover,
		&:visited {
			text-decoration: none;
			color: #FFF; } }
	&__logo {
		@include caslon;
		font-size: 33px;
		color: #fff;
		letter-spacing: -2.51px;
		line-height: 0.7;
		&:hover,
		&:focus {
			text-decoration: none;
			color: #fff; } } }
