.dara-footer {
	font-size: 14px;
	background: #fff;
	color: #000;
	&__in {
		max-width: 1178px;
		margin: 0 auto;
		padding: 43px 0 40px;
		@include rmin(768) {
			padding-bottom: 20px; } }
	&__logo {
		@include caslon;
		font-size: 34px;
		color: #167A65;
		letter-spacing: -1.94px;
		&:hover,
		&:focus {
			text-decoration: none;
			color: #167A65; } }
	&__title {
		margin-bottom: 30px;
		@include gilroy-bold;
		font-size: 25px;
		color: #167A65;
		line-height: 1.2; }
	&__form {
		margin-bottom: 20px; }
	&__row {
		position: relative; }
	&__input {
		display: block;
		width: 100%;
		height: 40px;
		padding: 0 60px 0 10px;
		@include gilroy;
		border-radius: 5px;
		background: #F5F6FA; }
	&__submit {
		position: absolute;
		top: 50%;
		right: 26px;
		background: none;
		transform: translateY(-50%);
		line-height: 0.7; }
	&__info {
		@include gilroy-medium;
		font-size: 11px;
		line-height: 1.2; } }


.dara-copyright {
	display: flex;
	flex-wrap: wrap;
	margin-bottom: 30px;
	@include gilroy-medium;
	font-size: 11px;
	line-height: 2;
	letter-spacing: 1.18px;
	@include rmin(768) {
		margin-bottom: 0; }
	&__option {
		margin-right: 20px; }
	&__link {
		color: #000;
		&:hover,
		&:focus {
			color: #000; } } }


.dara-nav {
	margin-bottom: 40px;
	@include rmin(768) {
		margin-bottom: 82px; }
	&__list {
		margin-bottom: 0;
		font-size: 0; }
	&__option {
		margin-bottom: 7px; }
	&__link {
		text-transform: capitalize;
		@include gilroy-bold;
		font-size: 16px;
		color: #000;
		letter-spacing: -0.34px;
		&:hover,
		&:focus {
			text-decoration: none;
			color: #167A65; } } }


.dara-social {
	display: flex;
	margin-bottom: 40px;
	@include rmin(768) {
		margin-bottom: 70px; }
	&__option {
		margin-right: 20px; }
	&__link {
		display: inline-block;
		width: 24px;
		height: 24px;
		background-color: #C6C6C6;
		line-height: 1;
		@include vertical;
		white-space: nowrap;
		&:hover,
		&:focus {
			.icon {
				fill: #fff; } } }
	.icon {
		vertical-align: middle;
		padding-left: 1px;
		font-size: 21px;
		line-height: 0.7;
		&.icon-twitter {
			font-size: 18px; } } }
